.navbar-b,
.navbar-r {
  position: relative;
  width: 100%;
  background-color: white !important;
}

.left-container-c {
  flex: 1;
  display: flex;
  height: 100%;
  align-items: center;
}

.middle-container-c {
  flex: 1;
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.navbar-light .navbar-nav .nav-link {
  font-family: 'Open Sans', sans-serif !important;
  font-weight: 600 !important;
}

.navbar-light .navbar-brand {
  font-family: 'Open Sans', sans-serif !important;
  font-weight: 600 !important;
}

.navbar-b .navbar-toggler {
  border-color: #212121;
}

.navbar-r .navbar-toggler {
  border-color: #212121;
}

.navbar-w .navbar-toggler {
  border-color: #fff;
}

.navbar-b .navbar-toggler:hover {
  border-color: #0f4c82;
}

.navbar-r .navbar-toggler:hover {
  border-color: #e3a143;
}

.navbar-w .navbar-toggler:hover {
  border-color: #e6e6e6;
}

.navbar-position {
  position: relative;
}

.navbar-b,
.navbar-r {
  padding-right: 2rem !important;
  padding-left: 2rem !important;
  padding-top: 1rem !important;
  padding-bottom: 0 !important;
}

.navbar-w {
  padding-right: 2rem !important;
  padding-left: 2rem !important;
  padding-top: 1rem !important;
  padding-bottom: 1rem !important;
}

.nav-center {
  position: absolute;
  left: 50%;
}

.right-container-c {
  flex: 1;
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: flex-end;
}

.logo-c {
  font-family: 'Open Sans', sans-serif;
  white-space: nowrap;
  color: white;
  font-weight: bold;
  text-decoration: none;
  font-size: 25px;
}

.logo-c:hover {
  cursor: pointer;
  color: white;
  text-decoration: none;
}

.navbar-item-c {
  text-decoration: none;
}

.navbar-item-c:hover {
  text-decoration: none;
  cursor: pointer;
}

.navbar-item-button-c {
  color: white;
  text-decoration: none;
}

.navbar-item-button-c:hover {
  color: white;
  text-decoration: none;
  cursor: pointer;
}

.navbar-item-width-height {
  width: 100%;
  height: 100%;
}

.navbar-b .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30'%3E%3Cpath stroke='rgba(33, 33, 33, 1)' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}

.navbar-r .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30'%3E%3Cpath stroke='rgba(33, 33, 33, 1)' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}

.navbar-w .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30'%3E%3Cpath stroke='rgba(255, 255, 255, 1)' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}

.navbar-b .navbar-toggler-icon:hover {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30'%3E%3Cpath stroke='rgba(15, 76, 130, 1)' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}

.navbar-r .navbar-toggler-icon:hover {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30'%3E%3Cpath stroke='rgba(227, 161, 67, 1)' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}

.navbar-w .navbar-toggler-icon:hover {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30'%3E%3Cpath stroke='rgba(230, 230, 230, 1)' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}

.navbar-signin-c {
  transform: scale(1);
  width: 100%;
  height: 30px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
  padding-right: 10px;
  padding-left: 10px;
  padding-top: 0;
  padding-bottom: 0;
}

.navbar-b .navbar-signin-colour {
  background-image: linear-gradient(to right, #0f4c82, #2a70ae);
  color: white;
}

.navbar-r .navbar-signin-colour {
  background-image: linear-gradient(to left, #e3a143, #ff802c);
  color: white;
}

.navbar-w .navbar-signin-colour {
  background-color: white;
  color: #212121;
}

.navbar-signin-c:hover {
  transform: scale(1.025);
  transition: transform 0.2s ease-in-out;
}

.navbar-b .navbar-title {
  background: linear-gradient(to left, #0f4c82, #2a70ae);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5rem !important;
}

.navbar-r .navbar-title {
  background: linear-gradient(to left, #e3a143, #ff802c);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5rem !important;
}

.navbar-b .navbar-item-normal {
  color: #212121;
  font-size: 1.15rem !important;
}

.navbar-r .navbar-item-normal {
  color: #212121;
  font-size: 1.15rem !important;
}

.navbar-w .navbar-item-right {
  color: #fff;
  font-size: 1.15rem !important;
}

.navbar-b .navbar-item-normal:hover {
  color: #0f4c82;
  transition: color 0.2s ease-in-out;
}

.navbar-b .navbar-selected {
  color: #0f4c82;
  font-size: 1.15rem !important;
}

.navbar-b .navbar-selected:hover {
  color: #0f4c82;
}

.navbar-r .navbar-item-normal:hover {
  color: #e3a143;
  transition: color 0.2s ease-in-out;
}

.navbar-r .navbar-selected {
  color: #e3a143;
  font-size: 1.15rem !important;
}

.navbar-r .navbar-selected:hover {
  color: #e3a143;
}

.navbar-w .navbar-item-right:hover {
  color: #e3a143;
  transition: color 0.2s ease-in-out;
}

.navbar-w .navbar-item-left:hover {
  color: #e3a143;
  transition: color 0.2s ease-in-out;
}

.navbar-signup-c {
  width: 100%;
  height: 30px;
  border-radius: 8px;
  border-style: none;
  background-color: transparent;
  cursor: pointer;
  transition: background-color 0.1s ease-in-out;
  padding-right: 10px;
  padding-left: 10px;
  padding-top: 0;
  padding-bottom: 0;
}

.navbar-b .navbar-signup-c {
  border: 1px #212121 solid;
  color: #212121;
}

.navbar-r .navbar-signup-c {
  border: 1px #212121 solid;
  color: #212121;
}

.navbar-w .navbar-signup-c {
  border: 1px #fff solid;
  color: #fff;
}

.navbar-b .navbar-signup-c:hover {
  color: #0f4c82;
  border: 1px #0f4c82 solid;
  transition: all 0.1s ease-in-out;
}

.navbar-r .navbar-signup-c:hover {
  color: #e3a143;
  border: 1px #e3a143 solid;
  transition: all 0.1s ease-in-out;
}

.navbar-w .navbar-signup-c:hover {
  color: #e6e6e6;
  border: 1px #e6e6e6 solid;
  transition: all 0.1s ease-in-out;
}

.navbar-b #collasible-nav-dropdown {
  color: #212121 !important;
  border: 1px #212121 solid;
  border-radius: 8px;
  transition: all 0.1s ease-in-out;
}

.navbar-r #collasible-nav-dropdown {
  color: #212121 !important;
  border: 1px #212121 solid;
  border-radius: 8px;
  transition: all 0.1s ease-in-out;
}

.navbar-w #collasible-nav-dropdown {
  color: #fff !important;
  border: 1px #fff solid;
  border-radius: 8px;
  transition: all 0.1s ease-in-out;
}

.navbar-b #collasible-nav-dropdown:hover {
  color: #0f4c82 !important;
  border: 1px #0f4c82 solid;
  transition: all 0.1s ease-in-out;
}

.navbar-r #collasible-nav-dropdown:hover {
  color: #e3a143 !important;
  border: 1px #e3a143 solid;
  transition: all 0.1s ease-in-out;
}

.navbar-w #collasible-nav-dropdown:hover {
  color: #e6e6e6 !important;
  border: 1px #e6e6e6 solid;
  transition: all 0.1s ease-in-out;
}

.navbar-wave {
  position: absolute;
  top: 100%;
  left: 0px;
  width: 100%;
  height: 45px;
  transform: scaleY(-1);
}
.navbar-wave-1 {
  z-index: 2;
}

.navbar-wave-2 {
  z-index: 1;
}

.nav-inner {
  width: 100%;
  z-index: 10;
  position: absolute;
}

@media (min-width: 992px) {
  .navbar-w {
    position: relative;
    width: 100%;
    background: transparent !important;
  }

  .navbar-w .navbar-title {
    background: linear-gradient(to left, #0f4c82, #2a70ae);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 1.5rem !important;
  }

  .navbar-w .navbar-item-left {
    color: #2a70ae;
    font-size: 1.15rem !important;
  }
}

@media (max-width: 991px) {
  .navbar-w {
    position: relative;
    width: 100%;
    background-image: linear-gradient(
      to right,
      rgba(15, 76, 130, 0.8),
      rgba(42, 112, 174, 0.8)
    ) !important;
  }

  .navbar-w .navbar-title {
    color: white;
    font-size: 1.5rem !important;
  }

  .navbar-w .navbar-item-left {
    color: white;
    font-size: 1.15rem !important;
  }
}
